/* POPUP */
.dnnFormPopup {
  position: absolute;
  padding: 1.125rem;
  background: var(--dnn-color-background, #fff);
  box-shadow: 0 0 25px 0 rgba(var(--dnn-color-foreground-r, 0), var(--dnn-color-foreground-g, 0), var(--dnn-color-foreground-b, 0), 0.75);
  z-index: 100000;
