}
.dnnFormPopup .dnnFormPopup > * {
  box-sizing: content-box;
}
.dnnFormPopup .ui-dialog-titlebar {
  position: relative;
  padding: 0 0 1.125rem 0;
  border-bottom: 1px solid var(--dnn-color-foreground-light, #ddd);
  cursor: move;
  font-size: 1.125rem;
  font-weight: bold;
}
.dnnFormPopup .dnnModalCtrl {
  /* This is the min/max button and the close button in module popups like the module settings. */
  /* PNG images are used for the buttons so we need to keep a color that works with white overlay and fixed sizes.*/
  display: block;
  position: absolute;
  top: -30px;
  right: -30px;
  width: 69px;
  height: 26px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 0px 5px #666;
}
.dnnFormPopup .dnnModalCtrl .ui-dialog-titlebar-close {
  float: right;
  display: block;
  height: 18px;
  width: 30px;
  margin: 4px 4px 0 1px;
  overflow: hidden;
  border: none;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  background: #585858 url(../../../../../images/closeBtn.png) no-repeat;
  background-position: 8px 4px;
  text-indent: -9999em;
  min-width: 0 !important;
}
.dnnFormPopup .dnnModalCtrl .ui-dialog-titlebar-close:hover {
  background: var(--dnn-color-primary, #358EEA) url(../../../../../images/closeBtn.png) no-repeat;
  background-position: 8px 4px;
  cursor: pointer;
}
.dnnFormPopup .dnnModalCtrl a.dnnToggleMax {
  float: left;
  display: block;
  height: 18px;
  width: 30px;
  margin: 4px 0 0 4px;
  overflow: hidden;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  background: #585858 url(../../../../../images/resizeBtn.png) no-repeat;
  background-position: 10px 2px;
  text-indent: -9999em;
}
.dnnFormPopup .dnnModalCtrl a.dnnToggleMax:hover {
  background: var(--dnn-color-primary, #358EEA) url(../../../../../images/resizeBtn.png) no-repeat;
  background-position: 10px 2px;
}
.dnnFormPopup .ui-resizable-se {
  float: right;
  height: 24px;
  width: 24px;
  background: url(../../../../../images/modal-resize-icn.png) no-repeat bottom;
}
.dnnFormPopup .dnnDialog {
  padding: 10px;
}
.dnnFormPopup #iPopUp {
  width: 100% !important;
  padding-top: 10px;
}
.dnnFormPopup .ui-dialog-content, .dnnFormPopup #iPopUp .ui-dialog-content {
  position: relative;
  border: 0;
  padding: 0px;
  overflow: auto;
  background: var(--dnn-color-background, #fff);
  zoom: 1;
}
.dnnFormPopup .ui-dialog-buttonpane {
  margin: 0.5em 0 0 0;
  padding: 0.3em 1em 0em 0em;
  overflow: hidden;
  border-width: 1px 0 0 0;
