/* Progressbar */
/*
Currently used by the installer/upgrade wizards and module injection.
Purposely leaving hardcoded values for sizes and not using css-variables 
because it is used in the installer and rarely visible in the module injection.
*/
.dnnProgressbar {
  height: 25px !important;
  padding: 18px 10px 0 10px;
  border: 1px solid #c9c9c9;
  background: #fff;
