}

/* Javascript scroll pane, I believe our own flavor of it. */
.jspContainer {
  overflow: hidden;
  position: relative;
}

.jspPane {
  position: absolute;
}

.jspVerticalBar {
  position: absolute;
  top: 0;
  right: 0;
  width: 11px;
  height: 100%;
  background: #ccc;
}

.jspHorizontalBar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 11px;
  background: #ccc;
}

.jspVerticalBar *,
.jspHorizontalBar * {
  margin: 0;
  padding: 0;
}

.jspCap {
  display: none;
}

.jspHorizontalBar .jspCap {
  float: left;
}

.jspTrack {
  background: transparent;
  position: relative;
}

.jspVerticalBar .jspTrack {
  width: 10px;
  margin: 0 0 0 3px;
}

.jspHorizontalBar .jspTrack {
  height: 5px;
  margin: 3px 0 3px 0;
}

.jspVerticalBar .jspCap {
  display: block;
  height: 3px;
  width: 11px;
}

.jspHorizontalBar .jspCap {
  display: block;
  width: 3px;
  height: 11px;
}

.jspDrag {
  position: relative;
  top: 0;
  left: 0;
  border-radius: 3px 3px 3px 3px;
